<template>
  <div>
    <van-nav-bar title=" - " />
    <van-form @submit="onSubmit">
      <van-field
        v-model="formLogin.mobile"
        type="tel"
        label=""
        placeholder=""
        required
        :rules="[
          {
            required: true,
            message: '',
            trigger: 'onChange',
            pattern: /^1[3456789]\d{9}$/,
          },
        ]"
      />
      <van-field
        v-model="formLogin.code"
        type="password"
        label=""
        placeholder=""
        required
        :rules="[
          {
            required: true,
            message: '',
            trigger: 'onChange',
            pattern: /^\d{6}$/,
          },
        ]"
      />
      <div style="margin: 16px">
        <van-button
            round
            block
            type="info"
            native-type="submit"
            :loading="isLoding"
            :disabled="isLoding"
            loading-text="ing..."></van-button>
      </div>
    </van-form>
  </div>
</template>

<script>

import { mapActions } from 'vuex'
import { MyNotify } from '@/utils/Notify'

export default {
  name: 'Login',
  data () {
    return {
      formLogin: {
        mobile: '13888888888',
        code: '246810' // 246810
      },
      isLoding: false // 
    }
  },
  methods: {
    ...mapActions(['asyncLoginAction']),
    async onSubmit () {
      try {
        await this.asyncLoginAction(this.formLogin)
        MyNotify({ type: 'success', message: '' })
        // Layout
        this.$router.replace({
          path: this.$route.query.path || '/layout'
        })
      } catch (err) {
        MyNotify({ type: 'warning', message: '' })
      }
    }
  }
}
</script>

